import axios from 'axios'

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:8000'

// Types
export interface TestStep {
  step_number: number
  action: string
  expected_result: string
}

export interface TestCase {
  id: string
  title: string
  description: string
  project_id: string
  test_suite_id: string
  steps: TestStep[]
  expected_result: string
  priority: 'low' | 'medium' | 'high'
  tags: string[]
  status?: 'draft' | 'active' | 'archived'
  created_by: string
  created_at: string
  updated_at?: string
}

export interface TestSuite {
  id: string
  name: string
  description: string
  project_id: string
  test_plan_id: string
  tags: string[]
  created_by: string
  created_at: string
  updated_at?: string
}

export interface TestPlan {
  id: string
  name: string
  description: string
  project_id: string
  objectives: string[]
  tags: string[]
  created_by: string
  created_at: string
  updated_at?: string
}

// API functions
export const testCasesAPI = {
  list: async (projectId: string, suiteId?: string) => {
    const token = localStorage.getItem('access_token')
    const params = new URLSearchParams({ project_id: projectId })
    if (suiteId) params.append('test_suite_id', suiteId)

    const response = await axios.get(`${API_URL}/api/v1/test-cases/?${params}`, {
      headers: { Authorization: `Bearer ${token}` }
    })
    return response.data
  },

  get: async (id: string) => {
    const token = localStorage.getItem('access_token')
    const response = await axios.get(`${API_URL}/api/v1/test-cases/${id}`, {
      headers: { Authorization: `Bearer ${token}` }
    })
    return response.data
  },

  create: async (data: Omit<TestCase, 'id' | 'created_at' | 'updated_at'>) => {
    const token = localStorage.getItem('access_token')
    const response = await axios.post(`${API_URL}/api/v1/test-cases/`, data, {
      headers: { Authorization: `Bearer ${token}` }
    })
    return response.data
  },

  update: async (id: string, data: Partial<TestCase>) => {
    const token = localStorage.getItem('access_token')
    const response = await axios.put(`${API_URL}/api/v1/test-cases/${id}`, data, {
      headers: { Authorization: `Bearer ${token}` }
    })
    return response.data
  },

  delete: async (id: string) => {
    const token = localStorage.getItem('access_token')
    const response = await axios.delete(`${API_URL}/api/v1/test-cases/${id}`, {
      headers: { Authorization: `Bearer ${token}` }
    })
    return response.data
  },
}

